import SwiperIndex from '@/components/index/IndexSwiper.vue';
import Recommend from '@/components/index/Recommend.vue';
import Card from '@/components/common/Card.vue';
import CommodityList from '@/components/common/CommondityList.vue';
// 附属页面
import AffiliateIndex from '@/components/index/affiliate/AffiliateIndex.vue';
export default {
	data() {
		return {
			topBarList:[],
			currentIndex:0,
			intoView:0,
			pageHight:0,
			newTopBar: []
		}
	},
	components:{
		SwiperIndex,
		Recommend,
		Card,
		CommodityList,
		AffiliateIndex
	},
	onLoad() {
		//请求接口数据
		this.__init();
	},
	onReady() {
		//获取系统信息，解决可视化高度的问题
		uni.getSystemInfo({
		    success: (res) => {
				//console.log(res.windowHeight);
				this.pageHight = res.windowHeight;
				// console.log(this.pageHight)
				console.log(res.platform)
		    }
		});
		
	},
	methods: {
		__init() {
			uni.request({
				url: 'http://192.168.101.144:3000/api/index_list/data',
				success: (res) => {
					let data = res.data.data;
					this.topBarList = data.topBar;
					this.newTopBar = this.__initData(data);
					// console.log(JSON.stringify(this.__initData(data)[0].data))
					// for(let k of this.__initData(data)[0].data) {
					// 	if(k.type === 'commodityList') {
					// 		console.log(JSON.stringify(k.data))
					// 	}
					// }
				}
			})
		},
		getSystemInfo() {
			uni.getSystemInfoSync({
				success: (res) => {
					console.log(res.platform)
				}
			})
		},
		__initData(res) {
			// console.log(JSON.stringify(res));
			let arr = [];
			for(let i = 0; i < this.topBarList.length; i++) {
				let obj = {
					data:[]
				};
				if(i == 0) {
					obj.data = res.data;
				}
				arr.push(obj);
			}
			return arr;
		},
		topClick(index) {
			if(index===this.currentIndex) return;
			this.currentIndex = index;
			this.intoView = this.currentIndex;
		},
		//content轮播触发change事件
		swiperChange(e) {
			this.topClick(e.detail.current)
		}
	}
}
